  *{
  

  }
  
  .Login-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   background-color: rgb(0, 0, 0);
    height: 100vh;
    background-image: url('https://media.istockphoto.com/id/941753398/vector/abstract-black-background-geometric-texture.jpg?s=612x612&w=0&k=20&c=uEt45YlzSV1taSdsK15xJElJ8sC3zh_B7f0rinWfB68=');
    /* background-image: url('https://thumbs.dreamstime.com/b/repeated-pattern-light-blue-colour-wallpaper-background-repeated-figures-abstract-contemporary-modern-print-geometric-183396759.jpg'); */
    /* background-repeat: no-repeat;
    border-image-width: ; */


}



.Login-container>button{
    width: 100px;
    height: 30px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 10px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.left-div{
    flex: 0.5;
    text-align: right;

}
.right-div{
    flex: 0.5;
    text-align:left;
}
.input-tags input{
    display: block;
    margin: auto;
    margin-bottom: 10px;
    width: 70%;
    height: 40px;
    border-radius: 10px;
    border: none;
    text-align: center;
    
    /* border: 3px solid rgb(68, 255, 0); */
}
input[type=text]:focus {
    border: 0px;
  }
.input-tags{
    display: flex;
    flex-direction: column;
   
    width: 45%;
    border: 2px solid black;
    border-radius: 10px;
    /* background-color: black; */
    box-shadow:0 0 2px 2px #f9caca;
    height: 170px;
    
}
.input-tags h2{
    display: block;
    margin: auto;
    border: 2px solid black;
    width: 100%;
}
a{
    text-decoration: none;
    color: #f9caca;
    
}
.input-tags-text{
    color: white;
   margin: 15px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    font-size:larger ;
    color: #f9caca;
}
.more-text{
    color: white;
    display: flex;
    height: fit-content;
    box-shadow:0 0 2px 2px #f9caca;
    margin-top:40px ;
    justify-content: center;
    align-items: center;
    width: 40%;
    border-radius: 40px;
}
.more-text p{
    display: block;
    margin: 10px 0;
}
.Login-container>button:hover{

    box-shadow:0 0 1px 1px #f9caca;
    background-color: #f9caca;
}
@media screen and (max-width: 600px) {
   .input-tags{
    width: 80%;
   }
   .more-text{
    width: 80%;
   }
  }  


  .correct-icon{
    border: 3px solid rgb(68, 255, 0);
}
.wrong-icon{
    border: 1px solid red;

}

@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');


  



