.cmt-card{
    width: 255px;
    border-radius: 10px;
    justify-content: space-between;
    margin: 10px;
    margin-left: 30px;
    margin-bottom: 40px;
    background-color: white;
    display: flex;
    flex-direction: column;
}
.cmt-card img{
    /* width: 100%; */
    height: 150px;
    width: 250px;
    border-radius: 10px 10px 0 0;
}
/* .inner-div{
    width: 255px; 
} */
.cmt-title_card{
    display: flex;
    
}
.cmt-channel_avatar{
    display: flex;
   /* border: 1px solid green; */
flex: 0.2;
justify-content: center;
align-items: center;
}
.cmt-channel_avatar>img{
   
     width: 80%;
     border-radius: 20px;
}
.cmt-thumbnail{
    /* border: 1px solid rgb(128, 0, 0); */
    flex: 0.8;
    padding-top: 10px;
    padding-right: 20px;
    padding-left: 10px;
}
.cmt-thumbnail h3{
    font-size: small;
}
.cmt-thumbnail p{
    font-size: small;
}
.cmt-Home_time,.cmt-Home_views{
    display: inline-block;

}
.cmt-Home_views{
    margin-left: 10px;

}
.cmt-thumbnail-title{
    margin-bottom: 10px;
}
.add-btn{
    width: 98%;
    height: 30px;
    background-color: white;
    border: 1px solid black;
    margin: 15px 3px;
}
.cmt-card>button{
    border-radius: 15px;
    
    background-color: white;
}

.cmt-card>button:hover{
    cursor: pointer;
     background-color: rgb(227, 227, 227);
 }

