.Cart-page{
display: flex;
margin-top: 70px;

}
.Cart-container{
    flex: 0.75;
    display: flex;
    flex-direction: column;
}
.Total-price-section{
flex: 0.25;
margin: 5px;
/* border:1px solid black; */
padding: 0 5px;
padding-top: 10px;
}
.sub-total{
    /* border:1px solid black; */
    padding: 10px;
    background-color: rgb(234, 234, 234);
}
.sub-total button{
    width: 100%;
    height: 30px;
    margin: 15px 0;
    
}
.sub-total p{
    font-weight: bold;
}
@media screen and (max-width: 650px) {
    .Cart-page{
        flex-direction: column-reverse;
        
        }
}