.upper-container{
    display: flex;
   margin-top: 40px;

}
.product-container{
    margin-top: 90px;
    /* border: 1px solid rgb(236, 0, 0); */

}
.upper-container-image img{
    height: 100%;
    /* border: 1px solid rgb(47, 255, 0); */
    /* padding: 0 10px; */
    width: 100%;

}
.upper-container-image{
    margin: 0 20px;
    /* border: 1px solid black; */
}
.upper-container-data{
    flex: 0.6;
    padding: 20px 15px;

}
.upper-container-data-des{
    margin-bottom: 15px;
    padding-right: 20px;
}
.upper-container-data-title{
    margin-bottom: 20px;
    font-weight: bold;
}
.upper-container-image{
    flex: 0.4;
    height: 400px;
    /* border: 1px solid rgb(242, 0, 0); */
    display: flex;
    justify-content: center;
    background-color: white;
}
.container-images{
    margin-top: 30px;
    margin-bottom: 50px;
    height: 180px;
    display: flex;
    /* border: 1px solid rgb(242, 0, 0); */
    justify-content: space-evenly;
    overflow:auto;
    white-space: nowrap;
    margin-left: 10px;
}
.container-images img{
    height: 100%;
    /* border: 1px solid rgb(0, 0, 0); */
    border-radius: 10px;
    margin: 0 10px;
    
}
.buy-btn{
    margin-top: 20px;
    width: 110px;
    height: 30px;
    border-radius: 13px;
}
.add-cart-btn{
    margin-left:15px ;
    width: 110px;
    height: 30px;
    border-radius: 13px;
}
.product-page-btns{
    /* border: 1px solid rgb(0, 0, 0); */
}
@media screen and (max-width: 570px) {
    .upper-container{
        flex-direction: column;
    }
}
.price-data{
    font-size: x-large;
}
.container-features{
    /* border: 1px solid green; */
    margin:30px 40px;
    padding: 10px 10px;
}

.feature-block{
    margin: 7px 0;
}
