.trending_card{
    display: flex;
    width: 100%;
    margin:15px 0;
    background-color: rgb(226, 226, 226);
    height: 220px;
    padding: 10px 0;
    
}
.trending_thumbnail{
    flex: 0.3;
    /* border: 2px solid rgb(248, 0, 0); */
    height: 100%;
    margin: 0 15px;
   
    
}
.trending_thumbnail img{
    /* width: 300px; */
    width: 100%;
    height: 100%;
    border-radius: 20px;

}
.trending_text img{
    width: 35px;
    height: 35px;
    border-radius: 20px;
    
}
.trending_text {
    flex: 0.8;
    /* border: 2px solid rgb(0, 248, 70); */
    padding-left: 15px;
    
}
.trending_card button{
    width: 20px;
    height: 10px;
    background-color: black;
}
@media screen and (max-width: 650px) {
    /* .trending_thumbnail{
        flex: 0.45;
    } */
    .trending_text {
        /* border: 1px solid rgb(0, 2, 128); */
    }
     .trending_card{
       flex-direction: column;
       height: 420px;
       /* border: 1px solid green; */
    }
    .trending_thumbnail{
        height: 50%;
    }
}

.page-btns{
    /* border: 1px solid black ; */
    display: flex;
    margin-top: 50px;
}
.page-btns button{
    margin: 0 10px;
    width: 120px;
    height: 30px;
    background-color: white;
    border: none;
    border-radius: 10px;
}
.trending_text p{
    margin-top: 10px;
}
