.amazon-header{
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    /* border: 1px solid rgb(0, 255, 187); */
    height: 70px;
    /* width: 80%; */
    padding: 7px 10px;
    /* margin-top: 20px; */
    width: 100%;
     position: fixed;
   z-index: 101;
   /* position: sticky; */
   top: 0;
   background-color: white;
   
   
    

}
.header-search{
    width: 80%;
    /* border: 1px solid rgb(17, 0, 255); */
    display: flex;
    justify-content: center;
    align-items: center;
    /* position: fixed; */
   
}
.amazon-header>img{
    object-fit: contain;
    width: 100px;
   
}
.amazon-header>img:hover{
    cursor: pointer;
}

@media screen and (max-width: 650px) {
    .amazon-header>img{
        width: 70px;
    }
    .header-option{
        
        font-size: larger;
    }
    .header-nav{
        width: 80%;
    }
}
@media screen and (min-width: 650px) {

    .header-nav{
        width: 80%;
    }
}

.header-serchInput{
    width: 100%;
    height: 25px;
  
}
.header-search>input{
    border-radius: 30px;
   
}

.header-nav{
    /* border: 1px solid rgb(0, 255, 17); */
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 7px 0;
}
.header-option{
    font-size: medium;
    font-size: x-small;
}
.header-option:hover{
    cursor: pointer;
    
}

.cart-option{
    display: flex;
}
.header-icon{
    font-size: 25px !important;
}