.product-block{
    flex-grow: 1;
    /* height: 300px; */
    margin: 7px 7px;
    background-color: white;
}
@media only screen and (max-width: 650px) {
    .product-block{
        margin-top: 17px;
        border: 1px solid rgb(198, 198, 198);
    }
}
.product-block>img{
    width: 100%;
    height: 150px;
}
.product-block>button{
    display: block;
    margin: auto;
    margin-bottom: 10px;
    border-radius: 15px;
    height: 25px;
    background-color: white;
}
.product-block>button:hover{
   cursor: pointer;
    background-color: rgb(227, 227, 227);
}
.product-block>p{
    margin: 10px;
}
.product-block>.price{
    margin-left: 30px;
}