
.body-banner{
    margin-top: 70px;
    width: 100%;
    height: 300px;
    margin-bottom: -30px;
    mask-image: linear-gradient(to bottom, rgb(0, 0, 0, 1),rgb(0, 0, 0, 0));
}
.home-body{
    /* border: 1px solid green; */
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}
@media only screen and (min-width: 900px) {
    .home-body {
        width: 85%;
    }

  }
  .product-col{
    display: flex;
    margin-top: 10px;
  }
  @media only screen and (max-width: 650px) {
    .product-col {
        flex-wrap: wrap;
        padding: 0 15px;
        
        }
  }
  .product-wrapped-col{
    flex-wrap: wrap;
  }
  
 
  @media only screen and (max-width: 450px) {
    .product-wrapped-col>.product-block{
        width: 100%;
      }
  }
  @media only screen and (min-width:450px) and  (max-width: 900px) {
    .product-wrapped-col>.product-block{
        width: 48%;
      }
  }
  @media only screen and (min-width: 900px) {
    .product-wrapped-col>.product-block{
        width: 30%;
      }
  }