

.Info-container{
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 70px;
    height: 85vh;

}

.Info-container table{
/* border: 1px solid green; */
width: 50%;
height: 65%;
display: flex;
flex-direction: column;
justify-content: space-between;
margin: 10px 0;

}
@media screen and (max-width: 800px) {
    .Info-container table{
        width: 95%;
    }
}
.Info-container tr{
border: 1px solid rgb(0, 0, 128);
display: flex;
height: 20%;
border-radius: 60px;
padding: 5px 0;
background-color: rgb(249, 222, 207);

}

.Info-container td{
/* border: 1px solid rgb(128, 0, 0); */
flex: 0.5;
display: flex;
justify-content: center;
align-items: center;
overflow: auto;
}
.hide_it{
    display: none;
}
.change_size{
    width:100%;
    height: 40%;
}
.small_td{
    flex: 0.1;
    /* border: 1px solid green; */
}
.data{
    /* border: 1px solid green; */
    flex: 0.9;
 
    text-align: center;
}
.Profile-container button{
    border-radius: 10px;
    margin-right: 5px;
}
.Info-container input{
    border-radius: 10px;
    width: 150px;
    
}