.signup-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   background-color: rgb(0, 0, 0);
    height: 100vh;
    background-image: url('https://media.istockphoto.com/id/941753398/vector/abstract-black-background-geometric-texture.jpg?s=612x612&w=0&k=20&c=uEt45YlzSV1taSdsK15xJElJ8sC3zh_B7f0rinWfB68=');

}
.signup-container>button{
    width: 100px;
    height: 30px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 10px;
    border: none;
}
.signup-input-tag input{
    display: block;
    margin: auto;
    margin-bottom: auto;
    width: 70%;
    height: 40px;
    /* margin-top:10px ;
    margin-bottom: 10px; */
    border-radius: 10px;
    border: none;
    text-align: center;
}
.signup-input-tag{
    display: flex;
    flex-direction: column;
   
    width: 40%;
    border: 2px solid black;
    border-radius: 10px;
    /* background-color: black; */
    box-shadow:0 0 2px 2px #f9caca;
    height: 300px;
}
.signup-container>button:hover{

    box-shadow:0 0 2px 2px #ededed;
}
.username-box{
display: flex;

align-items: center;
}
